import * as React from 'react';
import { NavLink } from "react-router-dom";
import translationService from '../services/translation.service';
import { getRoute } from '../services/routes.service';
import { categoriesRender } from '../route/productsCategoryRoutes';

interface IProps {
    cancel: () => void;
    isOpen: boolean;
}

export const SideBar = ({cancel, isOpen}: IProps) => {

    const className = (state:{isActive: boolean, isPending: boolean}) => {
        return "navItem " + (state.isActive ? "activeLink" : "");
    }
    
    return (
        <div className={`sidenav ${!isOpen ? "hiddenNav" : ''}`}>
            <div className='logoSidebarPosition'>
                <NavLink onClick={cancel} className={`${!isOpen ? "hidden" : ''}`} to={"/"}>
                    <img src='logo_main.svg'/>
                </NavLink> 
            </div>

            <div className='navItemsPosition'>
                {categoriesRender.map((category, id) => (
                    <NavLink onClick={cancel} className={className} to={getRoute(category.path)} key={id}>
                      {translationService.translate(category.translationKey)}
                    </NavLink>
                ))}
            </div>
        </div>
    );
};