import React, { useContext, useState } from 'react'
import {ReactComponent as SearchIcon} from '../images/search.svg';
import { ProductContext } from '../context/product-context';
import { getRoute } from '../services/routes.service';
import { useNavigate } from 'react-router';
import { ALL_DATA } from '../data/all_data';
import {ReactComponent as DeleteIcon} from '../images/cancel.svg';

const SearchField = () => {
  const { search, resetSearch, setInputValue, inputValue } = useContext(ProductContext);
  const [suggestions, setSuggestions] = useState<string[]>([]);
  const navigate = useNavigate();

  const handleInputChange = (event) => {
    const searchValue = event.target.value;
    search(searchValue);

    if (searchValue === '') {
      setSuggestions([]);
    } else {
      const filteredSuggestions = Array.from(
        new Set(
          ALL_DATA.map((a) => a.labelName).filter((example) =>
            example.toLowerCase().includes(searchValue.toLowerCase())
          )
        )
      );
      setSuggestions(filteredSuggestions);
    }
  };
  
  const handleClearAll = () => {
    setSuggestions([]);
    resetSearch();
  }
  
  return (
    <div className='relative'>
      <div className={`flex items-center border border-[#ccc] bg-white ${suggestions.length > 0 ? 'rounded-t-3xl' : 'rounded-3xl'} py-1 text-[#000]`}>
        <form role='search' onSubmit={() => navigate(getRoute(`search`))} className='flex items-center'>
          <input
            type="text" 
            id="searchInput" 
            className='py-2 px-4 w-[350px] bg-transparent focus:outline-none focus:border-none rounded-3xl'
            placeholder='Що тебе цікавить? Напр.: срібний браслет'
            aria-label='Що тебе цікавить? Напр.: срібний браслет'
            onChange={(event) => {
              setInputValue(event.target.value);
              handleInputChange(event);
            }}
            value={inputValue}
          />
          {suggestions.length > 0 ? <DeleteIcon className='pr-2 w-[35px]' onClick={handleClearAll}/> : <></>}
        </form>
        <div className='bg-[#ccc] w-[1px] h-[30px] '/>
        <span className='pr-5 pl-4' onClick={() => navigate(getRoute(`search`))}>
          <SearchIcon/>
        </span>
      </div>
      {suggestions.length > 0 ? (
        <div className='absolute bottom-[-160px] max-h-[160px] w-full overflow-y-auto bg-[#ffffff5e] p-2 rounded-b-3xl backdrop-blur-md'>
          {suggestions.map((suggestion, index) => (
            <div key={index} className='m-1 text-[#bebebe]'>
              {suggestion}
            </div>
          ))}
        </div>
      ) : <></>}
    </div>
  )
}

export default SearchField